.main {
  padding: 55px 0 180px;
  background: linear-gradient(#2395db 20%, #79e4f0 100%);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 7vh;
  --mask: radial-gradient(
        142.77px at 50% calc(100% - 168px),
        #000 99%,
        #0000 101%
      )
      calc(50% - 56px) 0/112px 100%,
    radial-gradient(142.77px at 50% calc(100% + 140px), #0000 99%, #000 101%)
      50% calc(100% - 28px) / 112px 100% repeat-x;
  -webkit-mask: var(--mask);
  mask: var(--mask);
  width: 100%;
  align-items: center;
}

.request {
  display: flex;
  flex-direction: row;
  gap: 56px;
}

.skyf-image {
  width: 9.2vw;
}

.content {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.cards {
  display: flex;
  flex-direction: row;
  gap: 12px;
}

.card {
  display: flex;
  flex-direction: row;
  width: 266px;
  height: 81px;
  align-items: center;
  gap: 0.75rem;
  background: #ffffff30;
  border-radius: 8px 8px 8px 4px;
  padding: 0 13px;
  justify-content: space-evenly;
}

.card__icon {
  min-width: 1.75rem;
  max-width: 1.75rem;
  stroke-width: 1.5px;
  fill: none;
  stroke: #fff;
}

.card__title {
  color: #fff;
  font-size: 16px;
  margin: 0;
  font-weight: 500;
  font-optical-sizing: auto;
  font-style: normal;
}

.card__arrow {
  width: 20px;
  height: 20px;
}

.input__box {
  width: 100%;
  padding: 0.75rem 0 0.75rem 0.75rem;
  position: relative;
  font-size: 1.2rem;
  color: #061c31;
  background: #fff;
  outline: none;
  border: none;
  border-radius: 8px;
  pointer-events: visible;
  display: flex;
  align-items: center;
}

.input {
  outline: none;
  border: none;
  width: 95%;
}

.input__icon {
  stroke: #0f6ab0;
  fill: none;
  width: 24px;
}

.tips {
  display: flex;
  justify-content: start;
  gap: 8px;
}

.tips__el {
  padding: 0.5rem 0.75rem;
  color: #fff;
  font-size: 0.8rem;
  background: linear-gradient(270deg, #61b8f3 0, #40a9ea 100%);
  border-radius: 0 5rem 5rem;
  cursor: pointer;
  text-decoration: none;
}
